import { Component, h, Element, State, Listen } from "@stencil/core"

@Component({
  tag: "sdx-text-truncate",
  styleUrl: "text-truncate.scss",
  shadow: true,
})
export class TextTruncate {
  private componentEl?: HTMLDivElement

  @Element() public el!: HTMLSdxTextTruncateElement

  @State() private isTruncated = false

  @Listen("mouseover")
  public onMouseOver() {
    if (!this.componentEl) {
      return
    }

    // reset state in case textContent changes from truncated text to another truncated text
    this.isTruncated = false

    const prevPosition = this.componentEl.style.position
    const width = this.componentEl.clientWidth

    // measure effective width
    this.componentEl.style.position = "absolute"
    const effectiveWidth = this.componentEl.clientWidth
    this.componentEl.style.position = prevPosition

    this.isTruncated = effectiveWidth > width
  }

  public render() {
    return (
      <div
        class="component"
        title={this.isTruncated ? this.el.textContent || "" : undefined}
        ref={(el) => (this.componentEl = el)}
      >
        <slot />
      </div>
    )
  }
}
