import {
  Component,
  h,
  Element,
  Prop,
  Watch,
  Listen,
  State,
  forceUpdate,
  Method,
  Fragment,
  Event,
  EventEmitter,
} from "@stencil/core"
import isNil from "lodash-es/isNil"
import flatpickr from "flatpickr"
import { Options as FlatpickrOptions } from "flatpickr/dist/types/options"
import { Instance as FlatpickrInstance } from "flatpickr/dist/types/instance"
import "flatpickr/dist/l10n/de.js"
import "flatpickr/dist/l10n/fr.js"
import "flatpickr/dist/l10n/it.js"
import {
  dispatchEvent,
  getNumericValue,
  getScrollContainerEl,
  parseFunction,
  getAppearance,
  animation,
} from "../../core/helpers/webcomponent-helpers"
import { InputType } from "./types"

@Component({
  tag: "sdx-input",
  styleUrl: "input.scss",
  shadow: true,
})
export class Input {
  private invokeHitEnterCallback: Function = () => null
  private invokeChangeCallback: Function = () => null
  private invokeInputCallback: Function = () => null
  private invokeFocusCallback: Function = () => null
  private invokeBlurCallback: Function = () => null
  private invokeKeyDownCallback: Function = () => null
  private flatpickrInstance?: FlatpickrInstance
  private inputEl?: HTMLInputElement | HTMLTextAreaElement | HTMLDivElement
  private componentDidLoadComplete = false
  private valueDidUpdate = false
  private resizeTimer?: NodeJS.Timer

  // A "native" hidden DOM element that is submitted when the item is used in a
  // traditional form.
  private lightDOMHiddenFormInputEl?: HTMLInputElement

  /**
   * Emitted whenever user is typing or clearing/changing the input.
   * @event
   */
  private static eventInput = "input"

  @Element() public el!: HTMLSdxInputElement

  @State() private hasInputElFocus: boolean = false

  /**
   * Text for the screen reader labelling the input field.
   */
  @Prop() public srHint: string = ""

  /**
   * Text for the screen reader labelling the search button.
   */
  @Prop() public srHintSearchButton: string = ""

  /**
   * Callback that will fire on hitting enter.
   * @deprecated use sdxsearch event instead.
   */
  @Prop() public hitEnterCallback?: (() => void) | string

  /**
   * Callback that will fire when the value has changed, regardless of method
   * (keyboard or programmatical).
   * @deprecated use input event instead.
   */
  @Prop() public changeCallback?: ((value: string) => void) | string

  /**
   * Callback that will fire when the value changes using the keyboard.
   * @deprecated use input event instead.
   */
  @Prop() public inputCallback?: ((value: string) => void) | string

  /**
   * Callback that will fire when the input gets focus.
   */
  @Prop() public focusCallback?: (() => void) | string

  /**
   * Callback that will fire when the input loses focus.
   */
  @Prop() public blurCallback?: (() => void) | string

  /**
   * Callback that will fire when a key is pressed down.
   */
  @Prop() public keyDownCallback?: ((e: CustomEvent) => void) | string

  /**
   * Default text that will be shown unless there's a value.
   */
  @Prop() public placeholder: string = ""

  /**
   * Default input field type (e.g. "search", "password", "date", etc.). When
   * set to "date", flatpickr will take over. Caution: flatpickr does not
   * fulfill the accessibility requirements of SDX, yet (but we're on it...).
   */
  @Prop() public type: InputType = "text"

  /**
   * Can be used for both reading and writing the value.
   */
  @Prop({ mutable: true }) public value: string = ""

  /**
   * Marks the text within the input on focus.
   */
  @Prop() public selectTextOnFocus: boolean = false

  /**
   * Disabled, but focussable using tab.
   */
  @Prop() public readonly: boolean = false

  /**
   * Disabled and not focussable using tab.
   */
  @Prop() public disabled: boolean = false

  /**
   * Text that will be written on the top.
   */
  @Prop({ reflect: true }) public label?: string

  /**
   * Allowed number of characters.
   */
  @Prop() public maxlength?: number

  /**
   * Datepicker settings.
   * When `type="date"`, flatpickr is used and can be configured via this
   * attribute. Examples:
   * `{ locale: "fr" }`, `{ minDate: "today" }` or
   * `{ "altInput": true, "altFormat": "l j. F, Y" }`.
   * See [Picker (date)](components_-_picker_(date).html#developers) for
   * available options.
   */
  @Prop() public flatpickrOptions: any

  /**
   * Name parameter (useful when the component is used in a traditional HTML
   * form submit).
   */
  @Prop() public name?: string

  /**
   * Set this to false to declare the component as invalid and use the
   * `validation-message` attribute to explain why.
   */
  @Prop() public valid?: boolean

  /**
   * Text that explains the validation status to the user.
   */
  @Prop() public validationMessage?: string

  /**
   * Marks the component as required (please note that this itself does not
   * handle validation &mdash; use the "valid" and "validation-message" for
   * that).
   */
  @Prop() public required: boolean = false

  /**
   * Focuses the input field as soon as it's rendered.
   */
  @Prop() public autofocus: boolean = false

  /**
   * Which keyboard to be displayed on touchscreens.
   */
  @Prop() public inputmode?: "decimal"

  /**
   * Renders an icon button for clearing the input value.
   * Not yet public because maybe it will become the default behavior, but
   * it's really hard to know which input fields are clearable and which ones
   * are not.
   * @private
   */
  @Prop() public clearable: boolean = false

  /**
   * Renders a div that looks exactly like an input field. Overflowing content
   * will have an ellipsis. Used for sdx-select.
   * Only implemented for `type="text"`.
   * @private
   */
  @Prop() public editable: boolean = true

  /**
   * @private
   */
  @Prop() public inputStyle: Record<string, string | undefined> = {}

  /**
   * Display a badge right to the text. Just for sdx-select.
   * @private
   */
  @Prop() public badge = ""

  /**
   * Disable animations for testing.
   * @private
   */
  @Prop() public animated: boolean = true

  @Watch("value")
  public valueChanged() {
    if (!this.componentDidLoadComplete) {
      return
    }

    this.valueDidUpdate = true

    // Transfer value into flatpickr
    this.flatpickrInstance?.setDate(this.value)

    this.updateHiddenFormInputEl()

    this.invokeChangeCallback(this.value)
  }

  @Watch("name")
  public nameChanged() {
    this.updateHiddenFormInputEl()
  }

  @Watch("hitEnterCallback")
  public hitEnterCallbackChanged() {
    this.setInvokeHitEnterCallback()
  }

  @Watch("changeCallback")
  public changeCallbackChanged() {
    this.setInvokeChangeCallback()
  }

  @Watch("inputCallback")
  public inputCallbackChanged() {
    this.setInvokeInputCallback()
  }

  @Watch("focusCallback")
  public focusCallbackChanged() {
    this.setInvokeFocusCallback()
  }

  @Watch("blurCallback")
  public blurCallbackChanged() {
    this.setInvokeBlurCallback()
  }

  @Watch("keyDownCallback")
  public keyDownCallbackChanged() {
    this.setInvokeKeyDownCallback()
  }

  @Watch("flatpickrOptions")
  public flatpickrOptionsChanged() {
    if (!this.componentDidLoadComplete) {
      return
    }

    this.updateFlatpickrInstance()
  }

  /**
   * Emitted when users press the enter key or clicks on search button when
   * `type="search"` is set.
   */
  @Event() public sdxsearch!: EventEmitter

  // This is a workaround needed because of a possible bug in ngModel:
  // It might be this bug: https://github.com/angular/angular/issues/13792
  // Without the workaround, this happens (testable in sdx-angular-example-deprecated):
  // End user types "asdf":
  // - changeCallback("asdf") --> correct
  // - [changeCallback]="onChange" executes onChange("asdf") --> correct
  // - ngModel="name" --> "asd" incorrect, the last letter is missing?
  @Listen("focus", { capture: true })
  public onFocus() {
    forceUpdate(this.el)
  }

  @Listen("keydown")
  public onKeyDown(e: KeyboardEvent) {
    this.onInputElKeyDown(e)
  }

  @Listen("click")
  public onClick() {
    this.openDatePicker(true)
  }

  @Listen("resize", { target: "window" })
  public onWindowResize() {
    if (this.type !== "date" || !this.getParsedFlatpickrOptions().weekNumbers) {
      return
    }
    clearTimeout(this.resizeTimer)
    this.resizeTimer = setTimeout(() => {
      const isOpen = this.flatpickrInstance?.isOpen
      // Workaround to fix calendar width
      this.updateFlatpickrInstance()
      if (isOpen) {
        this.openDatePicker()
      }
    }, 100)
  }

  /**
   * Set focus to element.
   */
  @Method()
  public async doFocus() {
    this.inputEl?.focus()
  }

  @Method()
  public async doBlur() {
    this.inputEl?.blur()
  }

  constructor() {
    this.positionDatePicker = this.positionDatePicker.bind(this)

    this.setInvokeHitEnterCallback()
    this.setInvokeChangeCallback()
    this.setInvokeInputCallback()
    this.setInvokeFocusCallback()
    this.setInvokeBlurCallback()
    this.setInvokeKeyDownCallback()

    this.updateHiddenFormInputEl()
  }

  public componentDidLoad() {
    this.updateFlatpickrInstance()
    this.updateHeight()

    if (this.autofocus) {
      // timeout because of https://github.com/ionic-team/stencil/issues/3246
      setTimeout(() => {
        this.doFocus()
      })
    }

    this.componentDidLoadComplete = true
  }

  public componentDidRender() {
    this.updateInputElValueIfOutOfSync()
  }

  public componentDidUpdate() {
    if (this.valueDidUpdate) {
      this.updateHeight()
      this.valueDidUpdate = false
    }
  }

  private onInputElKeyPress(e: Event) {
    const keyboardEvent: KeyboardEvent = e as KeyboardEvent

    if (keyboardEvent.key === "Enter") {
      this.invokeHitEnterCallback()
      this.sdxsearch.emit()
    }
  }

  private onInputElKeyDown(e: Event) {
    const keyboardEvent: KeyboardEvent = e as KeyboardEvent

    if (keyboardEvent.key === "Enter" || keyboardEvent.key === "ArrowDown") {
      this.openDatePicker()
    }

    this.invokeKeyDownCallback(e)
  }

  private focusDatePicker() {
    // focus to enable keyboard navigation
    const today = this.flatpickrInstance?.calendarContainer.querySelector(
      ".flatpickr-day.today"
    ) as HTMLSpanElement

    const selected = this.flatpickrInstance?.calendarContainer.querySelector(
      ".flatpickr-day.selected"
    ) as HTMLSpanElement

    selected ? selected.focus() : today.focus()
  }

  private openDatePicker(withoutFocus?: boolean) {
    if (!this.flatpickrInstance) {
      return
    }

    if (this.flatpickrInstance.isOpen) {
      !withoutFocus && this.focusDatePicker()
      return
    }

    this.flatpickrInstance!.open()
    !withoutFocus && this.focusDatePicker()

    const scrollContainerEl = getScrollContainerEl(this.el)
    if (scrollContainerEl) {
      // TODO remove this after SDX-941 (date picker optimizations)
      // Forces repositioning of Flatpickr to make sure it "sticks" to the
      // input field. The event isn't removed because it exists only once per
      // sdx-input.
      scrollContainerEl.addEventListener("scroll", this.positionDatePicker)
    }
  }

  private positionDatePicker() {
    this.flatpickrInstance?._positionCalendar()
  }

  private onInputElInput(e: Event): void {
    const value = (e.target as HTMLInputElement).value

    this.invokeInputCallback(value)
  }

  // Used when "inputCallback" prop is provided
  private customInputCallback(value: string) {
    parseFunction(this.inputCallback)(value)

    // After invoking the above callback, "this.value" is now updated.
    // If it holds another value than what the user has typed, overwrite
    // that users input with "this.value".
    // However, this is only possible *after* render, therefore a rerender is triggered.
    if (this.value !== value) {
      forceUpdate(this.el)
    }
  }

  // Used when *no* "inputCallback" prop is provided
  private defaultInputCallback(value: string): void {
    this.value = value
  }

  private setInvokeHitEnterCallback(): void {
    this.invokeHitEnterCallback = parseFunction(this.hitEnterCallback)
  }

  private setInvokeChangeCallback(): void {
    this.invokeChangeCallback = parseFunction(this.changeCallback)
  }

  private setInvokeInputCallback(): void {
    if (this.inputCallback) {
      this.invokeInputCallback = this.customInputCallback
    } else {
      this.invokeInputCallback = this.defaultInputCallback
    }
  }

  private setInvokeFocusCallback(): void {
    this.invokeFocusCallback = parseFunction(this.focusCallback)
  }

  private setInvokeBlurCallback(): void {
    this.invokeBlurCallback = parseFunction(this.blurCallback)
  }

  private setInvokeKeyDownCallback(): void {
    this.invokeKeyDownCallback = parseFunction(this.keyDownCallback)
  }

  private onInputElFocus(): void {
    this.hasInputElFocus = true

    // Readonly input fields can receive focus, but their height shouldn't change
    if (!this.readonly) {
      this.updateHeight()
    }

    if (this.selectTextOnFocus) {
      this.selectText()
    }

    this.invokeFocusCallback()
  }

  private onInputElBlur(): void {
    this.hasInputElFocus = false

    this.updateHeight()
    this.invokeBlurCallback()
  }

  private selectText(): void {
    setTimeout(() => {
      if (!this.isInputElement(this.inputEl)) {
        return
      }

      this.inputEl.setSelectionRange(0, this.inputEl.value.length)
    })
  }

  /**
   * Calculate and set the autocomplete height.
   * If it has content, it should shrink or grow based on the content (within
   * a certain range).
   */
  private updateHeight(): void {
    // Only change the height of textarea
    if (this.type !== "textarea") {
      return
    }

    const inputEl = this.inputEl!
    const paddingVertical =
      getNumericValue("padding-top", inputEl) +
      getNumericValue("padding-bottom", inputEl)
    const lineHeight = getNumericValue("line-height", inputEl)
    const border =
      getNumericValue("border-top-width", inputEl) +
      getNumericValue("border-bottom-width", inputEl)
    const maxHeight = paddingVertical + border + lineHeight * 10

    // Scrolling is disabled by default
    inputEl.style.overflow = "hidden"

    const prevHeight = inputEl.offsetHeight

    /*
      1. Shortly set the height to the current text content to take measurements
      2. "auto" can't be set, overridden or removed using anime.set(), see:
         https://github.com/juliangarnier/anime/issues/758
    */
    inputEl.style.removeProperty("height")

    // scrollHeight is initially 46px ($form-input-height)
    const heightPlusNewLine = inputEl.scrollHeight + border + lineHeight
    const newHeight = Math.min(heightPlusNewLine, maxHeight)

    // Activate scrolling if max height has been reached
    if (newHeight > maxHeight) {
      inputEl.style.overflow = "auto"
    }

    animation.set(inputEl, { height: prevHeight })

    animation.start({
      targets: inputEl,
      duration: this.componentDidLoadComplete && this.animated ? 150 : 0,
      height: newHeight,
    })
  }

  private updateHiddenFormInputEl(): void {
    if (!this.lightDOMHiddenFormInputEl) {
      this.lightDOMHiddenFormInputEl = document.createElement("input")
      this.lightDOMHiddenFormInputEl.type = "hidden"
      this.el.append(this.lightDOMHiddenFormInputEl)
    }

    // Clean up properties added previously in case they've changed
    this.lightDOMHiddenFormInputEl.removeAttribute("name")
    this.lightDOMHiddenFormInputEl.removeAttribute("value")

    if (this.name) {
      this.lightDOMHiddenFormInputEl.name = this.name
      this.lightDOMHiddenFormInputEl.value = this.value
    }
  }

  // Make sure the inputEl never has its "own" state but reflects "this.value".
  private updateInputElValueIfOutOfSync() {
    if (
      this.isInputElement(this.inputEl) &&
      this.value !== this.inputEl.value
    ) {
      // Just update the value if something has changed to avoid setting "" in
      // case inputEl has an invalid value.
      this.inputEl.value = this.value || ""
    }
  }

  private isInputElement(
    el?: HTMLElement
  ): el is HTMLInputElement | HTMLTextAreaElement {
    return !isNil(el) && "value" in el
  }

  private updateFlatpickrInstance(): void {
    if (this.type === "date") {
      const flatpickrOptions: FlatpickrOptions = {
        wrap: true, // enable calendar toggle icon
        allowInput: true,
        locale: "de",
        dateFormat: "d.m.Y", // e.g. "15.01.2017"
        time_24hr: true,
        clickOpens: false,
        monthSelectorType: "static",
        ...this.getParsedFlatpickrOptions(),
        // these attributes should not be overwritten by consumer:
        onChange: () => {
          // wait till flatpickr updated the input value
          setTimeout(() => dispatchEvent(this.el, Input.eventInput))
        },
        defaultDate: this.value,
      }

      this.flatpickrInstance = flatpickr(
        this.inputEl!.parentElement!, // { wrap: true } needs access to [data-input]
        flatpickrOptions
      )

      // copy some important attributes to altInput field
      if (flatpickrOptions.altInput) {
        for (let prop of Object.entries(this.getAltInputProps())) {
          const [propName, propValue] = prop
          this.flatpickrInstance?.altInput?.setAttribute(
            propName,
            propValue.toString()
          )
        }
      }
    }
  }

  private clear(): void {
    this.value = ""
    dispatchEvent(this.el, Input.eventInput)
  }

  private isClearable(): boolean {
    return this.clearable && this.editable
  }

  private isClearIconDisplayed(): boolean {
    return this.isClearable() && !!this.value
  }

  private createLabel() {
    const { value, maxlength, label, required } = this
    let counterEl: HTMLElement | undefined

    if (this.type === "textarea" && maxlength) {
      const valueLength = value ? value.length : 0

      counterEl = (
        <Fragment>
          <span aria-hidden="true" class="textarea-counter">
            {valueLength}/{maxlength}
          </span>
          <span role="status" class="sr-only">
            {maxlength - valueLength} characters left.
          </span>
        </Fragment>
      )
    }

    if (label || counterEl) {
      return (
        <label class={this.getLabelClassNames()} htmlFor="id">
          <span>
            {label}
            {required && <span aria-hidden="true">&nbsp;*</span>}
          </span>
          {counterEl}
        </label>
      )
    }
  }

  private getInputStyle(): Record<string, string | undefined> {
    if (this.isClearIconDisplayed()) {
      const paddingRight = parseFloat(this.inputStyle.paddingRight!) || 0

      return {
        ...this.inputStyle,
        paddingRight: `${paddingRight + 40}px`, // $baseline-5
      }
    }

    return this.inputStyle
  }

  // needed for flatpickr altInput feature
  private getAltInputProps() {
    return {
      autoCorrect: "off",
      autoCapitalize: "off",
      spellcheck: false,
      size: 1, // don't make sdx-input's width depending on its font-size
      "aria-invalid": `${this.valid === false}`,
      "aria-label": this.srHint,
      "aria-describedby": "additional-information",
      "aria-placeholder": "", // prevent sr from reading out as it's not helpful for blind people
    }
  }

  private getInputProps() {
    return {
      id: "id",
      name: this.name, // used to enable autofill on Chrome and Edge
      class: "input",
      placeholder: this.placeholder,
      maxLength: this.maxlength,
      style: this.getInputStyle(),
      required: this.required,
      disabled: this.disabled,
      readonly: this.readonly,
      ...this.getAltInputProps(),
      onInput: (e: Event) => this.onInputElInput(e),
      onKeyPress: (e: Event) => this.onInputElKeyPress(e),
      onFocus: () => this.onInputElFocus(),
      onBlur: () => this.onInputElBlur(),
      ref: (el?: HTMLInputElement | HTMLTextAreaElement) => (this.inputEl = el),
    }
  }

  private getLabelClassNames() {
    return {
      label: true,
      [getAppearance(this.el)]: true,
      readonly: this.readonly,
      disabled: this.disabled,
    }
  }

  private getComponentClassNames() {
    return {
      component: true,
      [this.type]: true,
      [getAppearance(this.el)]: true,
      readonly: this.readonly,
      disabled: this.disabled,
      editable: this.editable,
      clearable: this.clearable,
      valid: this.valid === true,
      invalid: this.valid === false,
    }
  }

  private onSearchButtonClick() {
    this.sdxsearch.emit()
  }

  private createSearchField() {
    return (
      <Fragment>
        <input
          {...this.getInputProps()}
          type={this.type}
          inputmode={this.inputmode}
        />

        <div class="inline-buttons">
          <div class="clear-button">
            <sdx-animation
              animationName={!!this.value ? "scale-in" : "scale-out"}
              animationDuration={this.animated ? undefined : 0}
            >
              <sdx-button
                theme="transparent"
                onClick={() => this.clear()}
                iconName="icon-close"
                aria-hidden="true"
                tabindex={-1}
              />
            </sdx-animation>
          </div>

          <sdx-button
            class="search-button"
            theme="transparent"
            onClick={() => this.onSearchButtonClick()}
            srHint={this.srHintSearchButton}
            iconName="icon-search"
            iconSize={3}
          />
        </div>
      </Fragment>
    )
  }

  private createInputField() {
    return (
      <Fragment>
        {this.editable && (
          <input
            {...this.getInputProps()}
            type={this.type}
            inputmode={this.inputmode}
          />
        )}

        {this.isClearable() && (
          <div class="inline-buttons">
            <div class="clear-button">
              <sdx-animation
                animationName={
                  this.isClearIconDisplayed() ? "scale-in" : "scale-out"
                }
                animationDuration={this.animated ? undefined : 0}
              >
                <sdx-button
                  theme="transparent"
                  onClick={() => this.clear()}
                  iconName="icon-close"
                  aria-hidden="true"
                  tabindex={-1}
                />
              </sdx-animation>
            </div>
          </div>
        )}

        {!this.editable && (
          <div
            class="input"
            style={this.inputStyle}
            tabIndex={this.disabled ? undefined : 0}
            ref={(el) => (this.inputEl = el)}
          >
            {this.value && <sdx-text-truncate>{this.value}</sdx-text-truncate>}

            {!this.value && this.placeholder && (
              <sdx-text-truncate class="placeholder">
                {this.placeholder}
              </sdx-text-truncate>
            )}

            <span class="badge">{this.badge}</span>
          </div>
        )}

        {this.editable && this.valid && (
          <sdx-icon
            class="checkmark-icon"
            iconName="icon-check-mark"
            size={3}
          />
        )}
      </Fragment>
    )
  }

  private createNumberInputField() {
    return <input {...this.getInputProps()} type="number" />
  }

  private createDateInputField() {
    return (
      <Fragment>
        <input {...this.getInputProps()} data-input />

        <sdx-button
          tabindex="-1"
          class="calendar-button"
          theme="transparent"
          srHint="Calendar"
          iconName="icon-calendar"
          iconSize={3}
          disabled={this.disabled || this.readonly}
          valid={!(this.valid === false && !this.hasInputElFocus)}
          onClick={(e) => {
            e.stopPropagation() // otherwise flatpickr on iOS thinks it lost focus and closes itself
            this.openDatePicker()
          }}
        />
      </Fragment>
    )
  }

  private createTextArea() {
    return <textarea {...this.getInputProps()} class="input textarea" />
  }

  /**
   * Parses and returns the flatpickrOptions prop.
   */
  private getParsedFlatpickrOptions(): FlatpickrOptions {
    let flatpickrOptions: FlatpickrOptions = {}

    if (this.flatpickrOptions) {
      if (typeof this.flatpickrOptions === "string") {
        flatpickrOptions = JSON.parse(this.flatpickrOptions)
      } else {
        // assume it's a valid flatpickr options object
        flatpickrOptions = this.flatpickrOptions
      }
    }

    return flatpickrOptions
  }

  public render() {
    return (
      <div class={this.getComponentClassNames()}>
        {this.createLabel()}

        <div class="wrapper">
          {(() => {
            switch (this.type) {
              case "textarea":
                return this.createTextArea()

              case "date":
                return this.createDateInputField()

              case "number":
                return this.createNumberInputField()

              case "search":
                return this.createSearchField()

              default:
                return this.createInputField()
            }
          })()}
        </div>

        <div id="additional-information">
          {this.validationMessage && (
            <Fragment>
              {/* extra span needed because validation-message is hidden on focus and therefore not read by screen reader */}
              <span class="sr-only">{this.validationMessage}</span>

              <sdx-validation-message
                validationMessage={this.validationMessage}
                aria-hidden="true"
              />
            </Fragment>
          )}
        </div>
      </div>
    )
  }
}
