import { Component, h, Element, Prop } from "@stencil/core"
import { getAppearance } from "../../core/helpers/webcomponent-helpers"

@Component({
  tag: "sdx-validation-message",
  styleUrl: "validation-message.scss",
  shadow: true,
})
export class ValidationMessage {
  @Element() public el!: HTMLSdxValidationMessageElement

  @Prop() public validationMessage: string = ""

  private getComponentClassNames() {
    return {
      component: true,
      [getAppearance(this.el)]: true,
    }
  }

  public render() {
    return (
      <div class={this.getComponentClassNames()}>
        <sdx-icon
          icon-name="icon-exclamation-mark"
          size={2}
          aria-hidden="true"
        />
        <span>{this.validationMessage}</span>
      </div>
    )
  }
}
